#app-container {
  position: relative;
  width: 100%;
  height: 100vh;
}

ul.menu {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;

    li {
        & > a {
            display: block;
            color: white;
            text-align: center;
            text-decoration: none;
        }
    }

    &.h {
        li {
            float: left;

            & > a {
                padding: 15px 25px;
            }

            &:hover {
                & > a {
                    color: rgb(10, 171, 245);
                }
            }
        }
    }

    &.v {
        li {
            padding: 15px 25px;
        }
    }
}